@keyframes modal-show{0%{opacity:0}100%{opacity:1}}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none;outline:none}ul,li,ol{list-style:none}*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;scroll-behavior:smooth}body{box-sizing:border-box;background-color:#191919;color:white}.card{display:block;border-radius:5%;overflow:hidden;cursor:pointer}.card__img-container{width:100%;height:220px;overflow:hidden}.card__photo{width:100%;height:100%;transition:transform 0.5s ease;object-fit:cover}.card__photo:hover{transform:scale(1.1)}.card__info{padding:20px;background-color:#2F2F2F;color:white;font-family:"Roboto",sans-serif}.card__title{padding:10px 0 20px 0;font-size:20px;font-family:"Dosis",sans-serif;font-weight:700}.card__body{display:flex;justify-content:flex-start;align-items:center;flex-direction:row}.card__circle{display:block;width:15px;height:15px;background-color:#252525;border:3px solid white;border-radius:50%}.card__circle--active{background-color:#17bf17}.card__status{margin-left:5px;display:flex;justify-content:flex-start;align-items:center;flex-direction:row}.card__status-info{font-size:1.5em;margin:0 4px}.header{display:grid;grid-template-columns:repeat(3, 1fr);box-shadow:10px 10px 15px -9px #8585