__hide{position:absolute;top:15px;right:15px;padding:0;-webkit-appearance:none;background-color:transparent;border:none;font-size:25px;text-align:center;color:black;background:#fff;width:25px;height:20px;padding:20px;text-decoration:none;border-radius:14px;font-weight:bold;display:flex;justify-content:center;align-items:center;flex-direction:row}.modal__info{background-color:#2F2F2F;display:flex;justify-content:center;align-items:center;flex-direction:column}@media only screen and (min-width: 50.25em){.modal__info{display:flex;justify-content:center;align-items:center;flex-direction:row}}.modal__photo{object-fit:cover;width:100%;padding:20px;border-radius:14px}.modal__bibliography{width:80%;display:flex;justify-content:center;align-items:normal;flex-direction:column;font-family:"Dosis",sans-serif;color:white;padding:10px 0 30px}@media only screen and (min-width: 50.25em){.modal__bibliography{margin-left:20px}}.modal__title{font-size:30px;font-weight:700;padding:10px 0 20px}.modal__status{display:flex;justify-content:flex-start;align-items:center;flex-direction:row}.modal__status-info{font-size:15px;margin-left:10px;text-align:center}.modal__status-race{margin-left:2px}.modal__circle{display:block;width:15px;height:15px;background-color:#252525;border:3px solid white;border-radius:50%}.modal__circle--active{background-color:#17bf17}.dimensions{padding:20px 0 0;font-size:15px}.dimensions-text{font-weight:600;padding-top:5px}.grid-cards{display:grid;grid-template-columns:repeat(4, minmax(300px, 300px));grid-row-gap:20px;row-gap:20px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}@media only screen and (min-width: 0){.grid-cards{grid-template-columns:repeat(1, minmax(300px, 300px))}}@media only screen and (min-width: 50.25em){.grid-cards{grid-template-columns:repeat(2, minmax(300px, 300px))}}@media only screen and (min-width: 75em){.grid-cards{grid-template-columns:repeat(3, minmax(300px, 300px))}}@media only screen and (min-width: 87.5em){.grid-cards{grid-template-columns:repeat(4, minmax(300px, 300px))}}.container{display:flex;justify-content:center;align-items:center;flex-direction:row;margin:50px 0 20px}

